<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="è´¦å·:" prop="loginId">
      <div >{{form.loginId}}</div>
    </el-form-item>
    <el-form-item label="å§“å:" prop="userName">
        <el-input v-model="form.userName" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="éƒ¨é—¨:" prop="userDept">
        <el-input v-model="form.userDept" auto-complete="off"></el-input>
    </el-form-item>

    <el-row>
      <el-col :span="12" style="text-align: right;"><el-button @click="close">å– æ¶ˆ</el-button></el-col>
      <el-col :span="12" style="text-align: left;"><el-button type="success":loading="submitLoading" @click="submit" style="float:left;color: #fdf7f7">ç¡® å®š</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'

@Component
export default class ModifyLoginPwdForm extends Vue{

  @Getter('auth/userName') userName

  @Prop({type: Object, required: false, default: () => ({})}) params//æŽ¥å—çˆ¶é¡µé¢çš„ä¼ è¿‡æ¥çš„å€¼
  @Prop({type: Function, required: true, default: null}) close//æŽ¥å—çˆ¶é¡µé¢çš„ä¼ è¿‡æ¥close
  submitLoading =false
  form = {loginId: '', userName: '',userDept:''}
  rules = {
    userName: [
      {trigger: "blur", required: true, message: 'è¯·è¾“å…¥å§“å'},
      {whitespace: true, message: 'ä¸èƒ½åŒ…å«ç©ºæ ¼'},
        {max:30 , message: 'é•¿åº¦ä¸èƒ½è¶…è¿‡30ä¸ªå­—ç¬¦'},
     {trigger: "blur,change", validator: (rule, value, callback) => {
         var regex =/^[\u4e00-\u9fa5]+$/
          if (regex.test(value)) return callback()
          return callback(new Error(`å§“ååªèƒ½æ˜¯æ±‰å­—`))
      }}
    ],
    userDept: [
      {trigger: "blur", required: true, message: 'è¯·è¾“å…¥éƒ¨é—¨'},
      {whitespace: true, message: 'ä¸èƒ½åŒ…å«ç©ºæ ¼'},
      {max: 64, message: 'é•¿åº¦ä¸èƒ½å¤§äºŽ64ä¸ªå­—ç¬¦'}
    ]
  }
    
  async submit() {
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return//éªŒè¯
    this.submitLoading = true;
     const rsp = await this.$helper.fetch('/api/userMgr/updateSysUser').setParams({...this.form}).send()
     this.submitLoading = false;
      if (!rsp.success) return rsp.success
      this.$notify({title: 'æˆåŠŸ',message:  'ç¼–è¾‘æˆåŠŸ',type: 'success',duration:4000});
     this.close(Math.random())
  }

  @Lifecycle created() {
   this.form = {...this.form, ...this.params}

    //  const {loginId = ''} = this.params
    //   this.form = {...this.form, loginId}
  }
}
</script>




