<template>
  <div class="contentBox">
    <bread-crumb/>
    <div class="custChoose container whiteBg">
      <self-grid ref="grid" url="/api/custMgr/selectCustList" v-model="queryForm" :multi="true">
        <template slot="query" slot-scope="{form, query, reset}">
          <el-form class="demo-form-inline" :inline="true" :model="form">
           <el-form-item label="å§“åï¼š">
              <el-input v-model="form.custName"></el-input>
            </el-form-item>
            <el-form-item label="èº«ä»½è¯å·ç ï¼š">
              <el-input  v-model="form.custCertNo"></el-input>
            </el-form-item>
            <el-form-item label="çŠ¶æ€ï¼š">
            <el-select v-model="form.mngStatus" clearable placeholder="è¯·é€‰æ‹©">
              <el-option v-for="(item, index) in codeStorage.dataSource('mngStatus')" :key="index" :label="item.label" :value="item.value"/>
           </el-select>
            </el-form-item>
            <el-form-item class="buttonTeam">
            <el-button type="primary" @click="query" >æŸ¥è¯¢</el-button>
              <!-- <el-button type="danger resetEditClass" @click="reset" >é‡ç½®</el-button> -->
              <el-button type="danger" @click="del">åˆ é™¤</el-button>
              <el-button type="success" @click="toManage">ç®¡ç†</el-button>
            </el-form-item>
          </el-form>
        </template>
        
        <!-- <el-table-column prop="userId" label="userid" style="display: none;"/> -->
        <el-table-column prop="custName" label="å§“å">
          <!-- <template slot-scope ="{row}">
             {{ row.custName.trim().substring(0,1)+"**"}}
          </template> -->
        </el-table-column>
        <el-table-column prop="custCertNo" label="èº«ä»½è¯" width="200">
           <template slot-scope ="{row}">
               {{row.custCertNo.replace( row.custCertNo.substr(6, 8), "****")}}
          </template>
        </el-table-column>
        <el-table-column prop="custTel" label="æ‰‹æœºå·">
           <template slot-scope ="{row}">
               {{row.custTel.replace( row.custTel.substr(3, 4), "****")}}
          </template>
        </el-table-column>
        <el-table-column prop="loanAmt" label="å€Ÿæ¬¾é‡‘é¢"/>
        <el-table-column prop="mngStatus" label="çŠ¶æ€">
           <template slot-scope="{row}">{{codeStorage.get('mngStatus', row.mngStatus)}}</template>
        </el-table-column>
        <el-table-column label="æ“ä½œ" width="100">
          <template slot-scope="{row, index}">
            <el-tooltip class="item" effect="light" content="ç¼–è¾‘"  placement="bottom-start" :visible-arrow="false">
              <el-button  type="text" class="iconfont icon-bianji" size="small"  @click.stop="userEdit(row)" style="color: #2ecc71;" ></el-button>
            </el-tooltip>

             <el-tooltip class="item" effect="light" content="æŸ¥çœ‹åŽ†å²æŠ¥å‘Š"  placement="bottom-start" :visible-arrow="false">
              <el-button  type="text" class="iconfont icon-baogaochakan" size="small"  @click.stop="seeHistoryReport(row)" style="color: #60b8f7;font-size: 21px;" ></el-button>
            </el-tooltip>
           
          </template>
        </el-table-column>
      </self-grid>
    </div>
     <self-dialog ref="serviceCustModInfoForm" title="ä¿®æ”¹ä¿¡æ¯" v-model="serviceCustModInfoVisible" @close="serviceCustModInfoForm">
      <template slot-scope="{close, params}">
        <service-cust-mod-info-form :close="close" :params="params"/>
      </template>
    </self-dialog>
  </div>
</template>
<script>
import Vue from "vue";
import {Component,InjectComponents,Prop,Watch,Lifecycle,Computed,Filter,State} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";


import serviceCustModInfoForm from './serviceCustModInfoForm'

//ç è¡¨
require('@/store/cache').cacheCodes('billType', 'roleType')
@Component
@InjectComponents({ BreadCrumb,serviceCustModInfoForm})
export default class AccManage extends Vue {
  //ä½¿ç”¨ç è¡¨
  @State(state => state.cache.codeStorage) codeStorage
  @State(state => state.cache.authStorage) authStorage
  queryForm = {}
  serviceCustModInfoVisible = false;
   form={toManage:'toManage',historyReport:'historyReport' }
   formHistoryReport ={toManage:'historyReport'}
//ç”¨æˆ·æ·»åŠ 
  toManage() {
     const rows = this.$refs.grid.selectedRows
     
   if(rows.length ==0) 
        return this.$notify({title: 'æç¤º',message:  'è¯·è‡³å°‘é€‰æ‹©ä¸€æ¡æ•°æ®',type: 'error',duration:4000});

  //  const dataFrom = {rows} //å’ŒmanageAccountä¿æŒä¸€è‡´
    const dataFrom = {...this.form,rows}
    this.$emit('open',dataFrom)
  }

  seeHistoryReport(row){
      const dataFrom = {...this.formHistoryReport,row}
    this.$emit('open',dataFrom)
  }
  //åˆ é™¤
 async del() {
   const rows = this.$refs.grid.selectedRows
   if(rows.length ==0) 
    return this.$notify({title: 'æç¤º',message:  'è¯·è‡³å°‘é€‰æ‹©ä¸€æ¡æ•°æ®',type: 'error',duration:4000});

   const arrayrows = []
    for (var i=0;i<rows.length;i++){
        arrayrows.push({'custId': rows[i].custId})
    }
    await this.$confirm("ç¡®å®šåˆ é™¤å½“å‰ç”¨æˆ·å—?")
    const rsp = await this.$helper.fetch("/api/custMgr/deleteCusts").setParam("custArray",arrayrows).send()
    if (!rsp.success) return
     this.$refs.grid.load({"pageNo":1});//é‡æ–°åŠ è½½åˆ—è¡¨
    this.$notify({title: 'æç¤º',message:  rsp.message,type: 'success',duration:4000});
  }
  //ç¼–è¾‘
  userEdit(row){
    this.serviceCustModInfoVisible =  true
    this.$refs.serviceCustModInfoForm.setParams(row)
  }

  serviceCustModInfoForm(){
     this.$refs.grid.load({"pageNo":1});//é‡æ–°åŠ è½½åˆ—è¡¨
  }

}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
    
</style>


