<template>
    <div class="custChoose contentBox">
        <div class="breadCrumb">
            <el-breadcrumb separator="ã€‹">
                <el-breadcrumb-item>ä¸šåŠ¡ç®¡ç†</el-breadcrumb-item>
                <el-breadcrumb-item >å®¢æˆ·ä¿¡æ¯</el-breadcrumb-item>
                <el-breadcrumb-item>å®¢æˆ·é€‰æ‹©</el-breadcrumb-item>
                </el-breadcrumb>
            <el-button type="warning" @click="() => $emit('close','11111')" style="float:right;margin-right: 20px;margin-top: -37px;line-height: 10px;">è¿”å›ž</el-button>
        </div>
    <div class="container whiteBg"> 
    <el-form ref="form" :model="form" label-width="80px">
        <el-row>
            <el-col :span="8" v-for="(item, index) in form.data.length" :key="item" :offset="index > 0 ? 2 : 0">
                <el-card :body-style="{ padding: '0px' }">
                <div style="padding: 14px;" class="cardHeader">
                    <el-checkbox  @change="(value) => change(index, value)">{{form.data[index].prodName}}</el-checkbox >
                    <span class="dangerLeve">é£ŽæŽ§çº§åˆ«:<span style="color: red;">{{form.data[index].riskLevel}}</span></span>
                </div>

                <img v-if="form.data[index].prodLibPath" class="image" :src="form.data[index].prodLibPath" style="height: 90px;"/>
                <img v-else class="image" src="../assets/custimg/img_fengkongbao.png"/>
                <div style="padding: 14px;" class="cardFooter">
                    <span>äº§å“ç®€ä»‹ï¼š{{form.data[index].prodRemark}}</span>
                    <div class="bottom clearfix cardFooter">
                    <span>é¢‘æ¬¡:</span>
                    <el-radio-group v-model="form.data[index].freType" class="elbutton">
                        <el-radio-button label="10011001">å¤©</el-radio-button>
                        <el-radio-button label="10011002">å‘¨</el-radio-button>
                        <!-- <el-radio-button :label="10011003">åŠæœˆ</el-radio-button> -->
                        <el-radio-button label="10011004">æœˆ</el-radio-button>
                    </el-radio-group>
                    <span class="button" v-if="form.data[index].saleNums">å·²è´­ä¹°{{form.data[index].saleNums}}</span>
                    <span class="button" v-else>å·²è´­ä¹°0</span>

                    </div>
                </div>
                </el-card>
            </el-col>
        </el-row>
            <div class="inputFoot">
                <el-button type="primary" @click="submit" :loading="submitLoading">æäº¤</el-button>
            </div>
     </el-form>
  
        </div>
    </div>
</template>
<script>
import Vue from "vue";

import {Component,InjectComponents, Prop, Watch,Lifecycle,Computed,Filter} from "vue-decorators";
import BreadCrumb from "@/components/breadCrumb";

require('@/store/cache').cacheCodes('freType')

@Component
@InjectComponents({ BreadCrumb })
export default class homepage extends Vue {
    activeName = 'second'
    labelPosition = 'right'
    form ={data:''}
    checkedData = new Set()
    serviceManageData = []
    submitLoading = false
    proImg = "../assets/custimg/img_fengkongbao.png"
    setParams(params = {}) {
    for (var i=0;i<params.rows.length;i++){
        this.serviceManageData.push({ "custId":params.rows[i].custId, "custName": params.rows[i].custName})
     }
    }
    change(index, {target: { checked = false} = {}} = {}){
        checked ? this.checkedData.add(index) : this.checkedData.delete(index)
    }
    async getList (){
        const rsp = await this.$helper.fetch('/api/prodMgr/queryProds').send()
        if (!rsp.success) return
        for(var m in rsp.data.list){ rsp.data.list[m].freType = "10011001"}
         this.form.data = rsp.data.list
    }

     async submit(){
        const fromData = this.form.data
        const newDataArray = []
        // const i = this.currentIndex
        //if (i < 0) return this.$message('è¯·é€‰æ‹©è‡³å°‘ä¸€æ¡æ•°æ®'); 
       
        const data = Array.from(this.checkedData)
            .map(index => this.form.data[index])
            .map(({prodId,prodName, freType}) => ({prodId,prodName, freType}))
           if(data.length ==0 ){
               return this.$alert(`è¯·é€‰æ‹©äº§å“`,'æ¸©é¦¨æç¤º')
           }
        // ä¿ç•™æ— æ•ˆçš„æ•°æ®
        const data2 = data.filter(({freType}) => freType == null)
            .map(({prodName}, index) => prodName)
            .join(",")
      
        if (data2.length != 0) return this.$alert(`è¯·é€‰æ‹©äº§å“"[${data2}]"çš„é¢‘æ¬¡`)
              
       // newDataArray.push({'prodId':fromData[i].prodId,'prodName':fromData[i].prodName,'freType':fromData[i].freType})
         const dataObject = {"custArray":this.serviceManageData,"prodArray":data}
          this.submitLoading =true
         const rsp = await this.$helper.fetch('/api/custMgr/saveCustProds').setParams(dataObject).send()
         this.submitLoading =false
        if (!rsp.success) return rsp.success
        this.$notify({title: 'æˆåŠŸ',message:  'å€ºåŠ¡ç®¡ç†æˆåŠŸ',type: 'success',duration:4000});
          this.$emit('close','11111')
    }

    @Lifecycle async created(){
        await this.getList()
       
    }
}
</script>
<style scoped>
.buttonTeam {float: right;margin: 0}
.inputFoot{border-top: 1px solid #ecf0f1;height: 59px}
.inputFoot .el-button--primary{float: right;width: 120px;margin-top: 23px}
.image { width: 100%;display: block;}
.button{float: right;margin-top: 20px;}
.dangerLeve{float: right}
.cardHeader{background-color: #efefef;height: 30px;line-height: 30px;}
.cardFooter{margin-top: 15px}
.el-card{border: 1px solid #c0c0c0;box-shadow: none}
span{color: #6b7074;}
.el-checkbox__label{font-size: 16px !important;}
.inputFoot{border-top: 1px solid #ecf0f1;height: 59px;margin-top: 30px;}
.inputFoot .el-button--primary{float: right;width: 120px;margin-top: 23px}
.button .el-button--primary{float: right;width: 120px;margin-top: 23px}

.el-col-offset-2{margin-left: 0}
.el-col-8{padding: 0 10px 20px 10px}
/* .elbutton .el-radio-button__orig-radio:checked+.el-radio-button__inner{background-color: #2ecc71;} */
.elbutton span{border:0px solid #bfcbd9;}
</style>


