export const undefined_ = 'undefined';
export const function_ = 'function';
export const string_ = 'string';
export const number_ = 'number';
export const object_ = 'object';
export const boolean_ = 'boolean';
export const null_ = null;

export const true_ = 'true';
export const false_ = 'false';

export const empty = '';
export const zero = 0;

export function isUndefined(value) { return typeof value === undefined_; }
export function isNotUndefined(value) { return !isUndefined(value); }

export function isNull(value) { return isUndefined(value) || value == null; }
export function isNotNull(value) { return !isNull(value); }

export function isUndefinedOrNull(value) {
  return isUndefined(value) || isNull(value);
}

export function isNotUndefinedOrNull(value) {
  return !isUndefinedOrNull(value);
}

export function isFunction(value) { return isNotNull(value) && typeof value === function_;}
export function isNotFunction(value) { return !isFunction(value); }

export function isString(value) { return isNotNull(value) && typeof value === string_;}
export function isNotString(value) { return !isString(value); }

export function isEmpty(value) { return isString(value) && value === empty; }
export function isNotEmpty(value) { return isString(value) && value !== empty; }

export function isBlank(value) { return isString(value) && value.trim() === empty; }
export function isNotBlank(value) { return isString(value) && value.trim() !== empty; }

export function isStartWith(value, startValue) { return isString(value) && isString(startValue) && value.indexOf(startValue) === zero; }
export function isNotStartWith(value, startValue) { return !isStartWith(value, startValue); }

export function isNotNumber(value) { return !isNumber(value); }

export function isObject(value) { return isNotNull(value) && typeof value === object_;}
export function isNotObject(value) { return !isObject(value); }

export function isArray(value) { return isObject(value) && value instanceof Array;}
export function isNotArray(value) { return !isArray(value); }

export function isBoolean(value) { return isNotNull(value) && typeof value === boolean_; }
export function isNotBoolean(value) { return !isBoolean(value);}

export function getUUID(len = 32, radix = 16) { 
  let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
  let uuid = [], i;
  radix = radix || chars.length;

  if (len) {
    // Compact form
    for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random()*radix];
  } else {
    // rfc4122, version 4 form
    let r;

    // rfc4122 requires these characters
    uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
    uuid[14] = '4';

    // Fill in random data.  At i==19 set the high bits of clock sequence as
    // per rfc4122, sec. 4.1.5
    for (i = 0; i < 36; i++) {
      if (!uuid[i]) {
        r = 0 | Math.random()*16;
        uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
      }
    }
  }

  return uuid.join('');
 }

export function trim(value) {
  if (isString(value)) return value.trim();
  return value;
}

export function extend(...args) {
  let isDeepClone = isBoolean(args[0]) ? args.shift() : false;
  if (args.length <= 1) return args[1];

  let target = null, clone;
  let keys, index, key;

  while(args.length > 0) {

    clone = args.shift();
    if (isNull(clone)) continue;
    if (isNull(target)) { target = clone; continue; }

    keys = Object.keys(clone);
    for (index = 0; index < keys.length; index++) {
      key = keys[index];
      target[key] = isDeepClone && isObject(clone[key])
        ? extend(true, isObject(target[key]) ? target[key] : {}, clone[key])
        : clone[key];
    }
  }

  return target;
}

