export function async(invoker, ...args) {
  return new Promise((resovle, reject) => {
    try { 
      invoker(resovle, ...args)
    } catch(e) {
      reject(e)
    }
  })
}

// æ•°å­—éªŒè¯(æ•´æ•°æˆ–å°æ•°)
export function isNumber(value) {
  return /^[-\+]?\d+$/.test(value) || /^[-\+]?\d+(\.\d+)?$/.test(value)
}

// æ•´æ•°éªŒè¯
export function isDigits(value) {
  return /^\d+$/.test(value)
}

// ä¸­æ–‡å­—ç¬¦éªŒè¯
export function isChinese(value) {
  return /^[\u0391-\uFFE5]+$/.test(value)
}

// éžä¸­æ–‡å­—ç¬¦éªŒè¯
export function isUnChinese(value) {
  return !/^[\u0391-\uFFE5]+$/.test(value)
}

// è‹±æ–‡å­—ç¬¦é›é˜µ
export function isEnglish(value) {
  return /^[A-Za-z]+$/.test(value)
}

// æ‰‹æœºéªŒè¯
export function isMobile(value) {
  return (value.length == 11 && /^(((13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8})$/.test(value))
}

// ç”µè¯å·ç éªŒè¯
export function isPhone(value) {
  return (/^(\d{3,4}-?)?\d{7,9}$/g).test(value)
}

// è”ç³»ç”µè¯(æ‰‹æœº/ç”µè¯çš†å¯)éªŒè¯   
export function isTel(value) {
  return isMobile(value) || isPhone(value)
}

// IPéªŒè¯
export function isIP(value) {
  return /^(([1-9]|([1-9]\d)|(1\d\d)|(2([0-4]\d|5[0-5])))\.)(([1-9]|([1-9]\d)|(1\d\d)|(2([0-4]\d|5[0-5])))\.){2}([1-9]|([1-9]\d)|(1\d\d)|(2([0-4]\d|5[0-5])))$/.test(value)
}

