<template>
         <!-- <div id="managedAcc" style="height: 462px;" ref="managedAcc"></div> -->

    <div id="managedAcc"  style="height: 462px;" v-loading="loading" element-loading-text="æŸ¥è¯¢ä¸­...">
            <div v-show="visable"  style="height: 462px;" ref="managedAcc"/>
            <div class="nohaveData"  v-show="!visable">
                    <img src="../assets/custimg/nodata.png"/>
                       <p>æš‚æ— æ•°æ®</p>
              </div>
        </div>


</template>
<script>
import Vue from 'vue'

import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter} from 'vue-decorators'
import echarts from 'echarts'
@Component
export default class HomePage extends Vue{

  visable = false
  loading =false

  async loadData(){
    this.loading = true
    const rsp = await this.$helper.fetch('/api/index/getLastWeekMngData').setParams({...this.form}).send()
    this.loading = false
    if (!rsp.success) return rsp.success
    this.visable = rsp.data.list.length != 0
    if (!this.visable) return

    const {managedAcc = {}} = this.$refs
    this.$nextTick(() => {
        this.managedAccCharts(managedAcc,{ title :"", optionsData : rsp } )
    })

}


    @Lifecycle mounted(){
        // echart
      this.loadData()

    }
    managedAccCharts(el, { title = "å¯å®¤å­¦ä¹ æ°›å›´æƒ…å†µè°ƒæŸ¥", optionsData = [], options = [] } = {}) {

    // const timeArray =['9.25', '9.26', '9.27', '9.28', '9.29','9.30','9.31']//æ—¶é—´
    // const yesArray =[320, 332, 301, 334, 390,30,50]//å·²ç®¡ç†
    // const noArray =[220, 182, 191, 234, 290,40,50]//æœ‰é£Žé™©
    const timeArray =[]//æ—¶é—´
    const yesArray =[]//å·²ç®¡ç†
    const noArray =[]//æœ‰é£Žé™©
    const datas = optionsData.data.list
     for(var i in datas){
      let abc = this.$helper.tools.formatDate(datas[i].busiDate, "YYYY-MM-DD").substring(5).replace('-','.');
      if(abc.substring(0,1) == '0') timeArray.push(abc.substring(1))
      else  timeArray.push(abc)
      yesArray.push(datas[i].mngCnt)
      noArray.push(datas[i].riskCnt)
      }



    const option = {
    backgroundColor: '#fff',
    color: ['#e74c3c', '#62b7f5'],
    calculable: true,
    legend: {x:'center',y:'bottom',data:['æœ‰é£Žé™©','å·²ç®¡ç†']},
     title: {
        text: 'äººæ•°',
        x:'15px',
        y:'20px',
        textStyle:{ color:'#c8c8c8',fontSize:'14px',}
    },
    tooltip : {
        trigger: 'axis',
        axisPointer : {            // åæ ‡è½´æŒ‡ç¤ºå™¨ï¼Œåæ ‡è½´è§¦å‘æœ‰æ•ˆ
            type : 'shadow'        // é»˜è®¤ä¸ºç›´çº¿ï¼Œå¯é€‰ä¸ºï¼š'line' | 'shadow'
        }
    },
    xAxis: [
      {
        type: 'category',
        axisTick: { show: false },
        axisLine: {lineStyle: {type: 'solid', color: '#cccccc',width:'1'}},
        data: timeArray
      }
    ],
    yAxis: [{
          axisLine: { 
          lineStyle: {type: 'solid',color:'#cccccc',width:'1' }
               },
        type: 'value'
      }
    ],
    series: [
       {
        name: 'æœ‰é£Žé™©',
        type: 'bar',
        barWidth : 20,//æŸ±å›¾å®½åº¦
        itemStyle: {
            normal: {barBorderRadius: 6,color: '#e74c3c',}
            },
        data: noArray
      },
      {
        name: 'å·²ç®¡ç†',
        type: 'bar',
        barGap: 0.2,
         barWidth : 20,//æŸ±å›¾å®½åº¦
        itemStyle: {
            normal: {barBorderRadius: 6, color: '#62b7f5',}
            },
        data: yesArray
      }
     
    ]
  }
  let myChart = echarts.init(document.getElementById("managedAcc"));
  myChart.setOption(option)
  window.addEventListener("resize",function () {//æ·»åŠ ä¸€ä¸ªå•å‡»äº‹ä»¶ï¼ˆå•å‡»æ—¶è§¦å‘functionå‡½æ•°ï¼‰  
      myChart.resize();
    })

    }
}

</script>
<style scoped>
.nohaveData{text-align: center;padding-top: 47%;}
.nohaveData p {color: #d9ddda;margin: 0;}
</style>


