<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="å§“å:" prop="custName">
      <el-input v-model="form.custName" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="èº«ä»½è¯:" prop="custCertNo">
        <el-input v-model="form.custCertNo" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="æ‰‹æœºå·:" prop="custTel">
        <el-input v-model="form.custTel" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="å€Ÿæ¬¾é‡‘é¢:" prop="loanAmt">
        <el-input v-model="form.loanAmt" auto-complete="off"></el-input>
    </el-form-item>
    <el-row>
      <el-col :span="12" style="text-align: right;"><el-button @click="close">å– æ¶ˆ</el-button></el-col>
      <el-col :span="12" style="text-align: left;"><el-button type="success" @click="submit" style="float:left;color: #fdf7f7" :loading= "submitLoading">ç¡® å®š</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'

@Component
export default class ModifyLoginPwdForm extends Vue{

  @Getter('auth/userName') userName

  @Prop({type: Object, required: false, default: () => ({})}) params//æŽ¥å—çˆ¶é¡µé¢çš„ä¼ è¿‡æ¥çš„å€¼
  @Prop({type: Function, required: true, default: null}) close//æŽ¥å—çˆ¶é¡µé¢çš„ä¼ è¿‡æ¥close
  submitLoading = false
  form = { custName: '',custCertNo:'',custTel:'',loanAmt:''}
  rules = {
    custName: [
      {trigger: "blur", required: true, message: 'è¯·è¾“å…¥å§“å'},
      {whitespace: true, message: 'ä¸èƒ½åŒ…å«ç©ºæ ¼'},
      {max: 30, message: 'é•¿åº¦ä¸èƒ½å¤§äºŽ30ä¸ªå­—ç¬¦'}
    ],
    custCertNo: [
      {trigger: "blur", required: true, message: 'è¯·è¾“å…¥èº«ä»½è¯'},
      {whitespace: true, message: 'ä¸èƒ½åŒ…å«ç©ºæ ¼'},
       {trigger: "blur,change", validator: (rule, value, callback) => {
         if (this.$helper.tools.isIdCard(value)) return callback()
        return callback(new Error("æ— æ•ˆèº«ä»½è¯"))
      }}
    ],
     custTel: [
      {trigger: "blur", required: true, message: 'è¯·è¾“å…¥ç”µè¯'},
      {whitespace: true, message: 'ä¸èƒ½åŒ…å«ç©ºæ ¼'},
       {trigger: "blur, change", validator: (rule, value, callback) => {
            if (value.length == 11 && /[0-9]{11}/.test(value)) return callback()
            return callback(new Error('è¯·è¾“å…¥é•¿åº¦ä¸º11ä½æœ‰æ•ˆçš„æ‰‹æœºå·ç '))
         }}
    ],
     loanAmt: [
        {trigger: "blur", required: false, message: 'è¯·è¾“å…¥å€Ÿæ¬¾é‡‘é¢'},
        {trigger: 'blur, change', max: 9, message: 'é•¿åº¦ä¸å¾—è¶…è¿‡9ä¸ªå­—ç¬¦' },
        {trigger: "blur, change", validator: (rule, value, callback) => {
          if(!value) return callback()
          // var isNum=/^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
          var isNum =   /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
          if (isNum.test(value)) return callback()
          return callback(new Error('è¯·è¾“å…¥æ­£ç¡®çš„é‡‘é¢'))
        }}
    ]
  }
    
  async submit() {
    if (!await this.$helper.tools.async(this.$refs['form'].validate)) return//éªŒè¯
    this.submitLoading = true
     const rsp = await this.$helper.fetch('/api/custMgr/updateCustInfo').setParams({...this.form}).send()
     this.submitLoading = false
      if (!rsp.success) return rsp.success
      this.$notify.success({title: 'æˆåŠŸ',message: 'ä¿å­˜æˆåŠŸ',duration:4000});
      this.close()
  }

  @Lifecycle created() {
    let {loanAmt =''} = this.params
    loanAmt = loanAmt.toString()//æŠŠloanAmtå­—æ®µå¤„ç†ä¸ºå­—ç¬¦
    const newParams = {...this.params,loanAmt}
    this.form = {...this.form, ...newParams}
  }
}
</script>




