<template>
  <div class="sendCodeForm">
      <el-form :model="form" ref="form" :rules="rules" label-width="110px" style="margin-top: 40px;" @keyup.enter.native="submit">
    <el-form-item label="éªŒè¯ç :" prop="msgCode" >
        <el-input placeholder="è¯·è¾“å…¥éªŒè¯ç "  v-model="form.msgCode" >
            <el-button slot="append" class="sendCode" @click="senCode" :disabled="!show" :loading="sendCodeLoading">
              <span v-show="show" >{{codeText}}</span>
              <span v-show="!show" class="count">{{count}} s</span>
              </el-button>
        </el-input>
    </el-form-item>
    <el-row style="margin-top: 50px;">
      <el-col :span="12" style="text-align: right;"><el-button @click="close">å– æ¶ˆ</el-button></el-col>
      <el-col :span="12" style="text-align: left;"><el-button type="success" :loading="submitLoading" @click="submit" style="float:left;color: #fdf7f7">ç¡® å®š</el-button></el-col>
    </el-row>         
  </el-form>

  </div>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter,Mutation,Action} from 'vue-decorators'

@Component
export default class ModifyLoginPwdForm extends Vue{

  @Getter('auth/userName') userName
  @Mutation("auth/setCurrentUserProfile") setCurrentUserProfile
  @Action('cache/run') cacheRun
  @State("auth") auth
  @Prop({type: Object, required: false, default: () => ({})}) params
  @Prop({type: Function, required: true, default: null}) close

  submitLoading =false
  sendCodeLoading =false

  count = ''
  show = true
  TIME_COUNT = 60;
  timer = null
  codeText = 'èŽ·å–éªŒè¯ç '

  form = {loginNo: '',loginPwd: '',msgCode:''}
  rules = {
      msgCode: [
        {trigger: "blur", required: true, message: 'è¯·è¾“å…¥éªŒè¯ç '},
        {trigger: "blur, change", validator: (rule, value, callback) => {
            if (value.length == 6 && /[0-9]{6}/.test(value)) return callback()
            return callback(new Error('è¯·è¾“å…¥6ä½æ•°çš„éªŒè¯ç '))
        }}
      ],
    }

    async senCode(){
      this.sendCodeLoading = true
      const rsp = await this.$helper.fetch('/api/login/getSmsMsgCode').setParams({...this.form}).send()
      this.sendCodeLoading =false
      if (!rsp.success) return rsp.success
      if (!this.timer) {
        this.count = this.TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= this.TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            this.codeText ='é‡æ–°å‘é€'
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000)
      }     
    }

    async submit() {
      if (!await this.$helper.tools.async(this.$refs['form'].validate)) return
      this.submitLoading = true
      const rsp = await this.$helper.fetch('/api/login').setParams({...this.form}).send()
      this.submitLoading =false
      if (!rsp.success) return rsp.success
      const {loginStatus = false, firstLogin = false,currentUserProfile} = rsp.data
      if (!loginStatus) return this.$notify({title: 'æç¤º',message:  'ç™»å½•å¤±è´¥',type: 'error',duration:4000});
        // æ‰§è¡Œç¼“å­˜
      await this.cacheRun()
      this.$notify({title: 'æˆåŠŸ',message:  'ç™»å½•æˆåŠŸ',type: 'success',duration:4000});
      this.setCurrentUserProfile(currentUserProfile)
      const {currentRouterName = 'index'} = this.auth
      this.$router.replace({name: currentRouterName})
        this.close(Math.random())
    }

      @Lifecycle created() {
        this.form = {...this.form, ...this.params}
  }
}
</script>
<<style scoped>
.sendCode{ padding: 0;width: 90px;margin: 0;border-radius: 0px !important;}
.sendCode span{line-height: 34px;}
</style>





