<template>
  <el-form :model="form" ref="form" :rules="rules" label-width="120px">
    <el-form-item label="è´¦å·:" prop="loginId">
      <div >{{form.loginId}}</div>
    </el-form-item>
    <el-form-item label="å¯†ç :" prop="userPwd">
        <el-input v-model="form.userPwd" auto-complete="off" type="password"></el-input>
    </el-form-item>

    <el-row>
      <el-col :span="12" style="text-align: right;"><el-button @click="close">å– æ¶ˆ</el-button></el-col>
      <el-col :span="12" style="text-align: left;"><el-button type="success" :loading="submitLoading"  @click="submit" style="float:left;color: #fdf7f7">ç¡® å®š</el-button></el-col>
    </el-row>         
  </el-form>
</template>
<script>
import Vue from 'vue'
import {Component, InjectComponents, Prop, Watch, Lifecycle, Computed, Filter, State, Getter} from 'vue-decorators'

@Component
export default class ModifyLoginPwdForm extends Vue{

  @Getter('auth/userName') userName

  @Prop({type: Object, required: false, default: () => ({})}) params
  @Prop({type: Function, required: true, default: null}) close
  submitLoading =false
  form = {loginId: '', userName: '',userDept:''}
  rules = {
   
    userPwd: [
      {trigger: "blur", required: true, message: 'è¯·è¾“å…¥å¯†ç '},
      {whitespace: true, message: 'ä¸èƒ½åŒ…å«ç©ºæ ¼'},
      {trigger: "blur,change", validator: (rule, value, callback) => {
          if (value.match("^[0-9a-zA-Z]+$")) return callback()
          return callback('åªèƒ½æ˜¯å­—æ¯æˆ–æ•°å­—')
      }}
    ],
  
  }
    
  async submit() {
   
     if (!await this.$helper.tools.async(this.$refs['form'].validate)) return//éªŒè¯
     this.submitLoading = true
     const newForms = {"userId":this.form.userId,"userPwd":this.form.userPwd}
     const rsp = await this.$helper.fetch('/api/userMgr/resetUserPwd').setParams(newForms).send()
     this.submitLoading =false
      if (!rsp.success) return rsp.success
      this.$notify({title: 'æˆåŠŸ',message:  'é‡ç½®å¯†ç æˆåŠŸ',type: 'success',duration:4000});
     this.close(Math.random())
  }

  @Lifecycle created() {
   this.form = {...this.form, ...this.params}

    //  const {loginId = ''} = this.params
    //   this.form = {...this.form, loginId}
  }
}
</script>




